import React, { useState, useEffect, useRef } from 'react';
import { MapContainer, TileLayer, Marker, Popup, useMap } from 'react-leaflet';
import L from 'leaflet';
import { Button } from './ui/button';
import { Card, CardContent, CardHeader, CardTitle } from './ui/card';
import { Badge } from './ui/badge';
import { Input } from './ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from './ui/select';
import { MapPin, Clock, Star, MessageCircle, Filter, Navigation, Users, Zap, CheckCircle } from 'lucide-react';
import { useToast } from '../hooks/use-toast';
import ProviderFilters from './ProviderFilters';

// Fix for default markers in react-leaflet
delete L.Icon.Default.prototype._getIconUrl;
L.Icon.Default.mergeOptions({
  iconRetinaUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-icon-2x.png',
  iconUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-icon.png',
  shadowUrl: 'https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-shadow.png',
});

// Custom icons for different service types
const createServiceIcon = (categoria, isSelected = false) => {
  const iconMap = {
    limpeza: '',
    manutencao: '',
    jardinagem: '',
    pintura: '',
    eletrica: '',
    encanamento: '',
    outros: ''
  };

  const emoji = iconMap[categoria] || '';
  const size = isSelected ? 40 : 30;
  const bgColor = isSelected ? '#3b82f6' : '#10b981';

  return L.divIcon({
    html: `
      <div style="
        background: ${bgColor};
        border: 3px solid white;
        border-radius: 50%;
        width: ${size}px;
        height: ${size}px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: ${size * 0.6}px;
        box-shadow: 0 2px 8px rgba(0,0,0,0.3);
        transform: translate(-50%, -50%);
      ">
        ${emoji}
      </div>
    `,
    className: '',
    iconSize: [size, size],
    iconAnchor: [size/2, size/2]
  });
};

// User location icon
const userIcon = L.divIcon({
  html: `
    <div style="
      background: #3b82f6;
      border: 4px solid white;
      border-radius: 50%;
      width: 20px;
      height: 20px;
      box-shadow: 0 2px 8px rgba(0,0,0,0.3);
      position: relative;
    ">
      <div style="
        position: absolute;
        top: -5px;
        left: -5px;
        width: 30px;
        height: 30px;
        border: 2px solid #3b82f6;
        border-radius: 50%;
        opacity: 0.3;
        animation: pulse 2s infinite;
      "></div>
    </div>
    <style>
      @keyframes pulse {
        0% { transform: scale(1); opacity: 0.3; }
        50% { transform: scale(1.2); opacity: 0.1; }
        100% { transform: scale(1); opacity: 0.3; }
      }
    </style>
  `,
  className: '',
  iconSize: [20, 20],
  iconAnchor: [10, 10]
});

const UberStyleMap = ({ user }) => {
  const [userLocation, setUserLocation] = useState(null);
  const [providers, setProviders] = useState([]);
  const [selectedProvider, setSelectedProvider] = useState(null);
  const [loading, setLoading] = useState(false);
  const [filters, setFilters] = useState({
    categoria: '',
    radius: 10,
    especialidade: '',
    disponibilidade: 'todos'
  });
  const [showFilters, setShowFilters] = useState(false);
  const [address, setAddress] = useState("");
  const [geocoding, setGeocoding] = useState(false);
  const mapRef = useRef();
  const { toast } = useToast();

  // API removida - usando dados mock

  // Get user's current location
  useEffect(() => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const location = {
            lat: position.coords.latitude,
            lng: position.coords.longitude
          };
          setUserLocation(location);
          updateUserLocation(location.lat, location.lng);
          loadNearbyProviders(location.lat, location.lng);
        },
        (error) => {
          // Error getting location
          // Default to So Paulo center if location access denied
          const defaultLocation = { lat: -23.5505, lng: -46.6333 };
          setUserLocation(defaultLocation);
          loadNearbyProviders(defaultLocation.lat, defaultLocation.lng);
          toast({
            variant: "destructive",
            title: "Localizao no disponvel",
            description: "Usando localizao padro. Permita acesso  localizao para melhor experincia.",
          });
        }
      );
    }
  }, []);

  const updateUserLocation = async (lat, lng) => {
    try {
      // Mock location update
    } catch (error) {
      // Error updating location
    }
  };

  const loadNearbyProviders = async (lat, lng) => {
    setLoading(true);
    try {
      const params = new URLSearchParams({
        latitude: String(lat),
        longitude: String(lng),
        radius_km: String(filters.radius)
      });
      if (filters.categoria) params.append('categoria', filters.categoria);

      const apiBase = import.meta.env.VITE_API_URL || (window.location.port === '3000' ? 'http://localhost:8000' : '');
      const resp = await fetch(`${apiBase}/api/providers/nearby?${params.toString()}`, {
        headers: { 'Accept': 'application/json' }
      });
      if (!resp.ok) throw new Error('Falha ao buscar prestadores');
      const data = await resp.json();
      let list = Array.isArray(data.providers) ? data.providers : [];
      
      // Aplicar filtros de especialidade e disponibilidade no frontend
      if (filters.especialidade) {
        list = list.filter(provider => 
          provider.especialidades && 
          provider.especialidades.some(esp => 
            esp.toLowerCase().includes(filters.especialidade.toLowerCase())
          )
        );
      }
      
      if (filters.disponibilidade !== 'todos') {
        list = list.filter(provider => {
          if (filters.disponibilidade === 'online') {
            return provider.disponivel === true;
          } else if (filters.disponibilidade === 'indisponivel') {
            return provider.disponivel === false;
          }
          return true;
        });
      }
      
      setProviders(list);
    } catch (error) {
      console.error('Error loading providers:', error);
      toast({
        variant: "destructive",
        title: "Erro ao carregar prestadores",
        description: "No foi possvel carregar os prestadores prximos.",
      });
    }
    setLoading(false);
  };

  // AHSW-21: Geocodificao por endereo (Nominatim / OpenStreetMap)
  const geocodeAddress = async () => {
    if (!address || address.trim().length < 3) {
      toast({
        variant: "destructive",
        title: "Endereo invlido",
        description: "Informe um endereo com pelo menos 3 caracteres.",
      });
      return;
    }
    setGeocoding(true);
    try {
      const q = encodeURIComponent(address.trim());
      const url = `https://nominatim.openstreetmap.org/search?q=${q}&format=json&limit=1`;
      const resp = await fetch(url, { headers: { 'Accept': 'application/json' } });
      const data = await resp.json();
      if (!Array.isArray(data) || data.length === 0) {
        toast({
          variant: "destructive",
          title: "Endereo no encontrado",
          description: "Tente ser mais especfico ou use um ponto de referncia.",
        });
        setGeocoding(false);
        return;
      }
      const lat = parseFloat(data[0].lat);
      const lng = parseFloat(data[0].lon);
      const newLoc = { lat, lng };
      setUserLocation(newLoc);
      if (mapRef.current) {
        const map = mapRef.current;
        if (map && map.setView) {
          map.setView([lat, lng], 14);
        } else if (map._leaflet_id && map._map) {
          // compatibility when ref points to internal object
          map._map.setView([lat, lng], 14);
        }
      }
      await loadNearbyProviders(lat, lng);
    } catch (error) {
      console.error('Erro ao geocodificar:', error);
      toast({
        variant: "destructive",
        title: "Erro ao buscar endereo",
        description: "Verifique sua conexo e tente novamente.",
      });
    }
    setGeocoding(false);
  };

  const handleFilterChange = () => {
    if (userLocation) {
      loadNearbyProviders(userLocation.lat, userLocation.lng);
    }
  };

  const handleNegotiate = async (provider, service) => {
    try {
      // Mock chat conversation
      toast({
        title: "Conversa iniciada! ",
        description: `Voc pode negociar diretamente com ${provider.nome}`,
      });

      // Here you would navigate to chat screen
    } catch (error) {
      toast({
        variant: "destructive",
        title: "Erro ao iniciar conversa",
        description: error.response?.data?.detail || "Tente novamente",
      });
    }
  };

  const getCategoryIcon = (categoria) => {
    const icons = {
      limpeza: '',
      manutencao: '',
      jardinagem: '',
      pintura: '',
      eletrica: '',
      encanamento: '',
      outros: ''
    };
    return icons[categoria] || '';
  };

  if (!userLocation) {
    return (
      <div className="h-screen bg-gray-900 flex items-center justify-center">
        <div className="text-white text-center">
          <Navigation className="h-12 w-12 mx-auto mb-4 animate-spin" />
          <p className="text-lg">Obtendo sua localizao...</p>
          <p className="text-sm text-gray-400 mt-2">Permita acesso  localizao para continuar</p>
        </div>
      </div>
    );
  }

  return (
    <div className="relative h-screen bg-gray-900 overflow-hidden">
      {/* Map Container */}
      <div className="absolute inset-0">
        <MapContainer
          center={[userLocation.lat, userLocation.lng]}
          zoom={14}
          className="h-full w-full"
          ref={mapRef}
          zoomControl={false}
        >
          <TileLayer
            url="https://{s}.basemaps.cartocdn.com/dark_all/{z}/{x}/{y}{r}.png"
            attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors &copy; <a href="https://carto.com/attributions">CARTO</a>'
          />
          
          {/* User location marker */}
          <Marker position={[userLocation.lat, userLocation.lng]} icon={userIcon}>
            <Popup>
              <div className="text-center">
                <p className="font-semibold"> Voc est aqui</p>
                <p className="text-sm text-gray-600">{user.nome}</p>
              </div>
            </Popup>
          </Marker>

          {/* Provider markers */}
          {providers.map((provider) => (
            provider.services.map((service) => (
              <Marker
                key={`${provider.provider_id}-${service.id}`}
                position={[provider.latitude, provider.longitude]}
                icon={createServiceIcon(service.categoria, selectedProvider?.provider_id === provider.provider_id)}
                eventHandlers={{
                  click: () => setSelectedProvider({...provider, selectedService: service})
                }}
              >
                <Popup>
                  <div className="min-w-64">
                    <div className="flex items-center gap-2 mb-2">
                      <span className="text-lg">{getCategoryIcon(service.categoria)}</span>
                      <div>
                        <h3 className="font-semibold">{provider.nome}</h3>
                        <p className="text-sm text-gray-600">{service.nome}</p>
                      </div>
                    </div>
                    
                    <div className="space-y-2">
                      <div className="flex justify-between">
                        <span className="text-sm"> Preo:</span>
                        <span className="font-semibold">R$ {service.preco_por_hora.toFixed(2)}/h</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-sm"> Tempo:</span>
                        <span className="text-green-600">{provider.estimated_time_min} min</span>
                      </div>
                      <div className="flex justify-between">
                        <span className="text-sm"> Avaliao:</span>
                        <span>{service.media_avaliacoes > 0 ? `${service.media_avaliacoes} (${service.total_avaliacoes})` : 'Sem avaliaes'}</span>
                      </div>
                    </div>
                    
                    <Button
                      onClick={() => handleNegotiate(provider, service)}
                      className="w-full mt-3 bg-blue-600 hover:bg-blue-700"
                      size="sm"
                    >
                       Negociar
                    </Button>
                  </div>
                </Popup>
              </Marker>
            ))
          ))}
        </MapContainer>
      </div>

      {/* Header */}
      <div className="absolute top-0 left-0 right-0 z-10 bg-gradient-to-b from-gray-900 to-transparent p-4">
        <div className="flex items-center justify-between text-white">
          <div>
            <h1 className="text-xl font-bold"> Encontrar Servios</h1>
            <p className="text-sm text-gray-300">{providers.length} prestadores prximos</p>
          </div>
          <div className="flex items-center gap-2">
            {/* Busca por endereo */}
            <Input
              value={address}
              onChange={(e) => setAddress(e.target.value)}
              placeholder="Digite um endereo para centralizar"
              className="w-64 bg-gray-800 border-gray-600 text-white placeholder:text-gray-400"
            />
            <Button
              variant="outline"
              size="sm"
              onClick={geocodeAddress}
              disabled={geocoding}
              className="bg-gray-800 border-gray-600 text-white hover:bg-gray-700"
            >
              {geocoding ? 'Buscando...' : 'Centralizar'}
            </Button>
            <ProviderFilters
              filters={filters}
              setFilters={setFilters}
              onApplyFilters={handleFilterChange}
              loading={loading}
              showFilters={showFilters}
              setShowFilters={setShowFilters}
            />
          </div>
        </div>

      </div>

      {/* Bottom Sheet - Provider List */}
      <div className="absolute bottom-0 left-0 right-0 z-10 bg-gray-900 border-t border-gray-700 max-h-96 overflow-hidden bottom-sheet">
        <div className="p-4">
          {/* Handle para arrastar */}
          <div className="w-12 h-1 bg-gray-600 rounded-full mx-auto mb-4 cursor-pointer"></div>
          
          {/* Header com contador e boto de finalizar */}
          <div className="flex items-center justify-between mb-4">
            <div>
              <h3 className="text-lg font-semibold text-white">
                {loading ? 'Buscando...' : `${providers.length} prestadores encontrados`}
              </h3>
              <p className="text-sm text-gray-400">Selecione um prestador para continuar</p>
            </div>
            {providers.length > 0 && (
              <Button
                onClick={() => {
                  // Lgica para finalizar seleo
                  if (selectedProvider) {
                    console.log('Finalizando seleo com prestador:', selectedProvider);
                    // Aqui voc pode adicionar lgica para navegar para a prxima etapa
                    // Por exemplo: navigate('/agendamento', { state: { provider: selectedProvider } });
                  } else {
                    console.log('Nenhum prestador selecionado');
                    // Mostrar mensagem para selecionar um prestador
                  }
                }}
                className="bg-green-600 hover:bg-green-700 text-white"
                size="sm"
                disabled={!selectedProvider}
              >
                <CheckCircle className="h-4 w-4 mr-2" />
                {selectedProvider ? 'Continuar' : 'Selecione um prestador'}
              </Button>
            )}
          </div>
          
          {loading ? (
            <div className="text-center py-8">
              <Users className="h-8 w-8 text-gray-400 mx-auto mb-2 animate-pulse" />
              <p className="text-gray-300">Buscando prestadores...</p>
            </div>
          ) : providers.length === 0 ? (
            <div className="text-center py-8">
              <Users className="h-8 w-8 text-gray-400 mx-auto mb-2" />
              <p className="text-gray-300">Nenhum prestador encontrado</p>
              <p className="text-sm text-gray-500">Tente aumentar o raio de busca</p>
            </div>
          ) : (
            <div className="space-y-3 max-h-64 overflow-y-auto scrollbar-thin scrollbar-thumb-gray-600 scrollbar-track-gray-800">
              {providers.map((provider) => (
                provider.services.map((service) => (
                  <Card 
                    key={`${provider.provider_id}-${service.id}`}
                    className="bg-gray-800 border-gray-700 cursor-pointer hover:bg-gray-750 transition-all duration-200 hover:scale-[1.02]"
                    onClick={() => {
                      setSelectedProvider({...provider, selectedService: service});
                      // Centralizar o item selecionado na tela
                      setTimeout(() => {
                        const element = document.querySelector(`[data-provider-id="${provider.provider_id}-${service.id}"]`);
                        if (element) {
                          element.scrollIntoView({ 
                            behavior: 'smooth', 
                            block: 'center' 
                          });
                        }
                      }, 100);
                    }}
                    data-provider-id={`${provider.provider_id}-${service.id}`}
                  >
                    <CardContent className="p-4">
                      <div className="flex items-center justify-between">
                        <div className="flex items-center space-x-3">
                          <div className="text-2xl">{getCategoryIcon(service.categoria)}</div>
                          <div>
                            <h3 className="font-semibold text-white">{provider.nome}</h3>
                            <p className="text-sm text-gray-300">{service.nome}</p>
                            {provider.especialidades && provider.especialidades.length > 0 && (
                              <div className="flex flex-wrap gap-1 mt-1">
                                {provider.especialidades.slice(0, 3).map((esp, index) => (
                                  <Badge key={index} variant="secondary" className="text-xs bg-blue-900 text-blue-200">
                                    {esp}
                                  </Badge>
                                ))}
                                {provider.especialidades.length > 3 && (
                                  <Badge variant="secondary" className="text-xs bg-gray-700 text-gray-300">
                                    +{provider.especialidades.length - 3}
                                  </Badge>
                                )}
                              </div>
                            )}
                            <div className="flex items-center space-x-4 mt-1">
                              <span className="text-sm text-green-400 flex items-center">
                                <Clock className="h-3 w-3 mr-1" />
                                {provider.estimated_time_min} min
                              </span>
                              <span className="text-sm text-yellow-400 flex items-center">
                                <Star className="h-3 w-3 mr-1" />
                                {service.media_avaliacoes > 0 ? service.media_avaliacoes : 'Novo'}
                              </span>
                              <span className={`text-sm flex items-center ${
                                provider.disponivel ? 'text-green-400' : 'text-red-400'
                              }`}>
                                <div className={`w-2 h-2 rounded-full mr-1 ${
                                  provider.disponivel ? 'bg-green-400' : 'bg-red-400'
                                }`}></div>
                                {provider.disponivel ? 'Online' : 'Indisponvel'}
                              </span>
                            </div>
                          </div>
                        </div>
                        
                        <div className="text-right">
                          <p className="font-bold text-white">R$ {service.preco_por_hora.toFixed(2)}</p>
                          <p className="text-xs text-gray-400">por hora</p>
                          <Badge variant="outline" className="mt-1 text-xs">
                            {provider.distance_km.toFixed(1)}km
                          </Badge>
                        </div>
                      </div>
                      
                      <div className="flex space-x-2 mt-3">
                        <Button
                          onClick={(e) => {
                            e.stopPropagation();
                            handleNegotiate(provider, service);
                          }}
                          className="flex-1 bg-blue-600 hover:bg-blue-700 text-sm"
                          size="sm"
                        >
                          <MessageCircle className="h-3 w-3 mr-1" />
                          Negociar
                        </Button>
                        <Button
                          variant="outline"
                          className="border-gray-600 text-gray-300 hover:bg-gray-700 text-sm"
                          size="sm"
                        >
                          <Zap className="h-3 w-3 mr-1" />
                          Agendar
                        </Button>
                      </div>
                    </CardContent>
                  </Card>
                ))
              ))}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default UberStyleMap;