// Utilitrios para plugins Cordova no Alca Hub
// Exemplo de uso dos plugins instalados

// Plugin de Cmera
export const cameraPlugin = {
  // Tirar foto
  async takePicture(options = {}) {
    if (window.navigator.camera) {
      return new Promise((resolve, reject) => {
        window.navigator.camera.getPicture(
          (imageData) => resolve(imageData),
          (error) => reject(error),
          {
            quality: 75,
            destinationType: window.Camera.DestinationType.DATA_URL,
            sourceType: window.Camera.PictureSourceType.CAMERA,
            ...options
          }
        );
      });
    }
    throw new Error('Plugin de cmera no disponvel');
  },

  // Escolher da galeria
  async chooseFromGallery(options = {}) {
    if (window.navigator.camera) {
      return new Promise((resolve, reject) => {
        window.navigator.camera.getPicture(
          (imageData) => resolve(imageData),
          (error) => reject(error),
          {
            quality: 75,
            destinationType: window.Camera.DestinationType.DATA_URL,
            sourceType: window.Camera.PictureSourceType.PHOTOLIBRARY,
            ...options
          }
        );
      });
    }
    throw new Error('Plugin de cmera no disponvel');
  }
};

// Plugin de Geolocalizao
export const geolocationPlugin = {
  // Obter posio atual
  async getCurrentPosition(options = {}) {
    if (window.navigator.geolocation) {
      return new Promise((resolve, reject) => {
        window.navigator.geolocation.getCurrentPosition(
          (position) => resolve(position),
          (error) => reject(error),
          {
            enableHighAccuracy: true,
            timeout: 10000,
            maximumAge: 60000,
            ...options
          }
        );
      });
    }
    throw new Error('Plugin de geolocalizao no disponvel');
  },

  // Monitorar posio
  async watchPosition(callback, options = {}) {
    if (window.navigator.geolocation) {
      return window.navigator.geolocation.watchPosition(
        callback,
        (error) => console.error('Erro de geolocalizao:', error),
        {
          enableHighAccuracy: true,
          timeout: 10000,
          maximumAge: 60000,
          ...options
        }
      );
    }
    throw new Error('Plugin de geolocalizao no disponvel');
  }
};

// Verificar se plugins esto disponveis
export const checkPlugins = () => {
  const plugins = {
    camera: !!window.navigator.camera,
    geolocation: !!window.navigator.geolocation
  };
  
  console.log('Plugins Cordova disponveis:', plugins);
  return plugins;
};

// Inicializar plugins quando o app carregar
export const initPlugins = () => {
  // Aguardar o Cordova estar pronto
  if (window.cordova) {
    document.addEventListener('deviceready', () => {
      console.log('Cordova plugins carregados:', checkPlugins());
    }, false);
  } else {
    console.log('Executando no navegador - plugins Cordova no disponveis');
  }
};
